.page {
  font-family: "Inter", Helvetica, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 27px;
  color: $clr--main;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
    padding: 0;
    min-width: 320px;
    background-color: $clr--bg;

    // &:has(.page__menu:target) {
    //   overflow: hidden;
    //   position: relative;
    //   height: 100%;
    // }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transition: transform 0.3s, opacity 0.3s;
    opacity: 0;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      display: block;
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}

.unscroll {
  overflow: hidden;
}
